import React from "react"
import { Link } from "gatsby"
import { Row, Col } from "react-bootstrap"

import Layout from "../components/layout"
import SEO from "../components/seo"
import "../stylesheet.css"
import { auto } from "@popperjs/core"

const IndexPage = () => (
  <Layout>
    <SEO title="Home" />
    <Row>
      <Col md="auto">
        <Link to={"/artwork"} style={{ textDecoration: "none" }}>
          <p className={"homeLinks"}>Artwork</p>
        </Link>
      </Col>
    </Row>
    <Row>
      <Col md={{ span: auto, offset: 3 }}>
        <Link to={"/dataviz"} style={{ textDecoration: "none" }}>
          <p className={"homeLinks"}>Data Visualizations</p>
        </Link>
      </Col>
    </Row>
    <Row>
      <Col md={{ span: auto, offset: 7 }}>
        <Link to={"/github"} style={{ textDecoration: "none" }}>
          <p className={"homeLinks"}>GitHub</p>
        </Link>
      </Col>
    </Row>
    <Row>
      <Col md={{ span: auto, offset: 9 }}>
        <Link to={"/about"} style={{ textDecoration: "none" }}>
          <p className={"homeLinks"}>About</p>
        </Link>
      </Col>
    </Row>
  </Layout>
)

export default IndexPage
